import React from "react";
import { useState, useEffect } from "react";
import { Form, Button, Col } from "react-bootstrap";
import FormContainer from "../components/FormContainer";
import CheckotuSteps from "../components/CheckoutSteps";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { savePaymentMethod } from "../slices/cartSlice";

const PaymentScreen = () => {
  //  state      default  "Paypal"
  const [paymentMethod, setPaymentMethod] = useState("Paypal");

  const dispatch = useDispatch();
  const navigate = useNavigate();

  //        Redux store
  const cart = useSelector((state) => state.cart);
  const { shippingAddress } = cart;

  //     ,    
  useEffect(() => {
    if (!shippingAddress) {
      navigate("/shipping");
    }
  }, [shippingAddress, navigate]);

  //    
  const submitHandler = (e) => {
    e.preventDefault(); //  default   ( )
    dispatch(savePaymentMethod(paymentMethod)); //      Redux store
    navigate("/placeorder"); //     
  };

  return (
    <FormContainer>
      {/*  checkout (step1, step2, step3 ) */}
      <CheckotuSteps step1 step2 step3 />
      <h1>Payment Method</h1>
      <Form onSubmit={submitHandler}>
        <Form.Group>
          <Form.Label as="legend">Select Method</Form.Label>
          <Col>
            {/*     radio button */}
            <Form.Check
              type="radio"
              className="my-2"
              label="Paypal or Credit Card"
              id="Paypal"
              name="paymentMethod"
              value="Paypal"
              checked //  
              onChange={(e) => setPaymentMethod(e.target.value)} //  state   
            ></Form.Check>
          </Col>
        </Form.Group>
        {/*    */}
        <Button type="submit" variant="primary">
          Continue
        </Button>
      </Form>
    </FormContainer>
  );
};

export default PaymentScreen;
