import { createSlice } from "@reduxjs/toolkit";

//  :     userInfo   localStorage,  
const initialState = {
  userInfo: localStorage.getItem("userInfo")
    ? JSON.parse(localStorage.getItem("userInfo"))
    : null,
};

const authSlice = createSlice({
  name: "auth", //  slice
  initialState, //  
  reducers: {
    //      credentials (..   login)
    setCredentials: (state, action) => {
      state.userInfo = action.payload; //      state
      localStorage.setItem("userInfo", JSON.stringify(action.payload)); //    localStorage
    },
    //     (logout)
    logout: (state, action) => {
      state.userInfo = null; //   userInfo  state
      //localStorage.removeItem("userInfo"); //   localStorage
      localStorage.clear();
    },
  },
});

//    (actions)       
export const { setCredentials, logout } = authSlice.actions;

//   reducer      store
export default authSlice.reducer;
